import React,{useState,useEffect} from 'react';
import './contacto.css';
import Intro from '../intro/intro';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


const Contacto = () => {

           //EMAIL
        const [email,setEmail]=useState((
                'E-mail'       
        ));
    
        const cambiarEmail =() =>{
            setEmail('slronzoni@gmail.com');
        }
    
        useEffect (()=>{
            console.log('muestro email de contacto')
        });
    
    
        //CELU
        const [celu,setCelu]=useState((
            'Telefono'
        ));
    
        const cambiarCelu =() =>{
             setCelu('15-xxxx-xxxx');
        }
    
        useEffect (()=>{
            console.log('muestro celu de contacto')
        });
    

    return(
        <div className="home">
            <Intro titulo="Silvina Ronzoni" />
            <Container>
                <Row className="text-center">
                    <Col sm={4} md={4}> 
                        <h3>{email}</h3>
                        <button className="button btn btn-primary" onClick={cambiarEmail}>E-mail</button>
                    </Col>

                    <Col sm={4} md={4}> 
                        <h3>{celu}</h3>
                        <button className="button btn btn-primary " onClick={cambiarCelu}>Teléfono</button>
                    </Col>
                </Row>                
            </Container>
        </div>
    );
  };

export default Contacto;

     

